.App-header {
    padding-bottom: 56px;
    min-width: 978px;
    position: relative;
  }
  
  .hd-sd{
    height: 100%;
    min-width: 124px;
  }
  
  .hd-cnt{
      max-width: 1248px;
      width: 100%;
      height: 100%;
      margin: 0 auto;
      display: flex;
      align-items: center;
  }
  
  .head-cnt{
    position: fixed;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex: auto;
    -ms-flex: auto;
    flex: auto;
    width: 100%;
    height: 56px;
    background-color: #32aeb1;
    color: #f0f0f0;
    z-index: 10;
    -webkit-flex-direction: row;
    -ms-flex-direction: row;
    flex-direction: row;
  }
  
  .logo{
    font-size: 30px;
    min-width: 137px;
  }
  
  .search-box{
      display: flex;
      -webkit-justify-content: flex-start;
      -ms-flex-pack: start;
      justify-content: flex-start;
      height: 100%;
      min-width: 304px;
      width: calc(100% - 540px);
      margin: 0 auto 0 12px;
      -webkit-align-items: center;
      -ms-flex-align: center;
      align-items: center;
  }
  
  .src-cnt{
    display: inline-block;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.23);
    width: 100%;
    height: 36px;
    max-width: 564px;
    position: relative;
  }
  .src{
    width: 100%;
    display: flex;
    display: flex;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-flex: auto;
    -ms-flex: auto;
    flex: auto;
    border-radius: 2px;
    background-color: #fff;
  }
  .src-in{
      padding: 0 16px;
      border-radius: 2px 0 0 2px;
      border: 0;
      outline: 0 none;
      font-size: 14px;
      height: 36px;
      width: 100%;
  }
  .user{
      margin: 0 20px;
      max-width: 150px
  }
  .usr-cnt{
      height: 100%;
      line-height: 20px;
      font-size: 16px;
      letter-spacing: .1px;
      font-weight: 500;
      color: #fff;
  }
  .cart{
    margin: 0 20px;
    max-width: 150px;
    cursor: pointer;
  }
  .cart-cnt{
    display: flex;
    height: 100%;
    line-height: 20px;
    font-size: 16px;
    letter-spacing: .1px;
    font-weight: 500;
    color: #fff;
  }
  
  .src-btn{
    justify-content: flex-end;
    border: 1px solid #fff;
    background-color: #fff;
    height: 36px;
    width: 44px;
    border-radius: 2px;
    cursor: pointer;
    padding: 4px 12px 0 8px;
  }